import { Component, Element, Host, Method, Prop, Watch, h } from '@stencil/core';

@Component({
	tag: 'o-option',
	styleUrl: 'o-option.scss',
	shadow: true
})
export class OOption {
	@Element() host: HTMLOptionElement;

	@Prop({ reflect: true, mutable: true }) value = '';

	@Prop({ reflect: true }) disabled: boolean = false;

	@Prop({ mutable: true }) selected: boolean = false;

	@Prop({ mutable: true }) current: boolean = false;

	@Watch('value')
	handleValueChange() {
		if (typeof this.value !== 'string') {
			this.value = String(this.value);
		}

		// replace spaces with underscores as spaces delimiters in multi-select scenarios
		if (this.value.includes(' ')) {
			this.value = this.value.replace(/ /g, '_');
		}
	}

	@Method()
	async getTextContent() {
		const nodes = Array.from(this.host.childNodes);
		let label = '';

		[...nodes].forEach((node) => {
			if (node.nodeType === Node.ELEMENT_NODE) {
				if (!(node as HTMLElement).hasAttribute('slot')) {
					label += (node as HTMLElement).textContent;
				}
			}

			if (node.nodeType === Node.TEXT_NODE) {
				label += node.textContent;
			}
		});

		return label.trim();
	}

	render() {
		const baseClasses = 'body-default relative flex items-center py-1 pl-2';

		const selectedClasses = 'bg-background-brand-knockout text-content-brand-knockout';

		const checkClasses = 'flex flex-none items-center justify-center';

		const disabledClasses = 'text-content-disabled cursor-not-allowed';

		const currentClasses =
			'bg-background-brand-knockout text-content-brand-knockout outline-focus-ring-color-border-default';

		return (
			<Host>
				<div
					aria-selected={this.selected ? 'true' : 'false'}
					aria-disabled={this.disabled ? 'true' : 'false'}
					class={`${baseClasses} ${this.selected && !this.disabled ? selectedClasses : ''} ${
						this.disabled ? disabledClasses : 'cursor-pointer'
					} ${!this.selected && !this.disabled && !this.current ? 'text-content-default hover:bg-background-default-hover' : ''} ${
						this.current ? currentClasses : ''
					}`}
				>
					<o-icon
						class={`${this.selected ? 'flex' : 'flex invisible'} ${checkClasses}`}
						name="check"
						color={this.disabled ? 'content-disabled' : this.current ? 'content-brand-knockout' : ''}
					></o-icon>
					<div class={`pl-2 flex-auto inline-block`}>
						<slot></slot>
					</div>
				</div>
			</Host>
		);
	}
}
